/* CLICKABLE CARD */
.clickable-card:not(a) {
  position: static;
}

.clickable-card:not(a) a {
  position: static;
}

.clickable-card:not(a) > a:after {
  content: "";
  position: absolute;
  inset: 0;
  cursor: pointer;
  display: flex;
}

a.clickable-card {
  position: static;
}

a.clickable-card:after {
  content: "";
  position: absolute;
  inset: 0;
  cursor: pointer;
  display: flex;
}

/* OUTLINE */
:focus,
button:focus {
  --outline-color: var(--alt);
  --outline-style: solid;
  --outline-width: 0.2em;
  --outline-offset: 0.2em;
