  outline-color: var(--outline-color);
  outline-width: var(--outline-width);
  outline-offset: var(--outline-offset);
}

:focus:not(:focus-visible),
button:focus:not(:focus-visible),
input:active {
  outline: none;
}

/* SCREEN READER ACCESSIBLE NO-DISPLAY */
.screen-reader {
  position: absolute;
  inline-size: 1px;
  block-size: 1px;
  padding: 0;
  margin: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
}

