@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  ::before,
  ::after {
    animation-delay: -1ms;
    animation-duration: 1ms;
    animation-iteration-count: 1;
